﻿@import "highlight.js/styles/github.css";

:root {
  color-scheme: light;
  --bg: #f5f1e9;
  --panel: #fff7ec;
  --panel-strong: #ffffff;
  --text: #1f1a14;
  --muted: #6b5c4a;
  --accent: #d47b3f;
  --accent-strong: #b85b1f;
  --border: #e3d6c6;
  --active-line: #f2e7d8;
  --selection: rgba(212, 123, 63, 0.25);
  --shadow: 0 20px 60px rgba(31, 26, 20, 0.12);
  --font-ui: "Palatino Linotype", "Book Antiqua", "Palatino", "Georgia", serif;
  --font-mono: "Cascadia Mono", "Iosevka", "Consolas", "Menlo", monospace;
}

@media (prefers-color-scheme: dark) {
  :root {
    color-scheme: dark;
    --bg: #14110c;
    --panel: #1f1a14;
    --panel-strong: #241e17;
    --text: #f2e9dd;
    --muted: #b7a996;
    --accent: #e29a5c;
    --accent-strong: #f1b67d;
    --border: #3a2f24;
    --active-line: #2a231a;
    --selection: rgba(226, 154, 92, 0.25);
    --shadow: 0 20px 60px rgba(0, 0, 0, 0.45);
  }
}

body.theme-light {
  color-scheme: light;
  --bg: #f5f1e9;
  --panel: #fff7ec;
  --panel-strong: #ffffff;
  --text: #1f1a14;
  --muted: #6b5c4a;
  --accent: #d47b3f;
  --accent-strong: #b85b1f;
  --border: #e3d6c6;
  --active-line: #f2e7d8;
  --selection: rgba(212, 123, 63, 0.25);
  --shadow: 0 20px 60px rgba(31, 26, 20, 0.12);
}

body.theme-dark {
  color-scheme: dark;
  --bg: #14110c;
  --panel: #1f1a14;
  --panel-strong: #241e17;
  --text: #f2e9dd;
  --muted: #b7a996;
  --accent: #e29a5c;
  --accent-strong: #f1b67d;
  --border: #3a2f24;
  --active-line: #2a231a;
  --selection: rgba(226, 154, 92, 0.25);
  --shadow: 0 20px 60px rgba(0, 0, 0, 0.45);
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: var(--font-ui);
  color: var(--text);
  background-color: var(--bg);
  background-image:
    radial-gradient(circle at 12% 18%, rgba(212, 123, 63, 0.12), transparent 40%),
    radial-gradient(circle at 88% 12%, rgba(115, 88, 55, 0.12), transparent 45%),
    linear-gradient(135deg, rgba(255, 255, 255, 0.25), rgba(0, 0, 0, 0.05));
  min-height: 100vh;
}

#app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 16px 18px 20px;
}

#app.no-preview .layout {
  grid-template-columns: 220px minmax(0, 1fr);
}

.topbar {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 12px 16px;
  background: var(--panel-strong);
  border-radius: 16px;
  border: 1px solid var(--border);
  box-shadow: var(--shadow);
}

.brand {
  font-size: 20px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  font-weight: 600;
}

.actions {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.status {
  margin-left: auto;
  font-size: 14px;
  color: var(--muted);
}

button {
  border: 1px solid transparent;
  border-radius: 999px;
  padding: 8px 16px;
  font-size: 14px;
  font-family: var(--font-ui);
  cursor: pointer;
  transition: transform 0.15s ease, box-shadow 0.2s ease, border-color 0.2s ease;
}

button:hover {
  transform: translateY(-1px);
}

button.primary {
  background: var(--accent);
  color: #1f1a14;
  box-shadow: 0 10px 20px rgba(212, 123, 63, 0.35);
}

button.primary:hover {
  border-color: var(--accent-strong);
}

button.ghost {
  background: transparent;
  color: var(--text);
  border-color: var(--border);
}

.layout {
  display: grid;
  grid-template-columns: 240px minmax(0, 1fr);
  gap: 12px;
  flex: 1;
  min-height: 0;
}

#app.split-view .layout {
  grid-template-columns: 240px minmax(0, 1fr) minmax(0, 1fr);
}

.sidebar {
  background: var(--panel);
  border-radius: 16px;
  padding: 16px;
  border: 1px solid var(--border);
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.sidebar-tabs {
  display: flex;
  gap: 8px;
}

.tab {
  flex: 1;
  border-radius: 12px;
  border: 1px solid var(--border);
  padding: 8px 12px;
  background: var(--panel-strong);
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.14em;
}

.tab.active {
  background: var(--accent);
  color: #1f1a14;
}

.sidebar-pane {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.sidebar-title {
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.18em;
  color: var(--muted);
}

.sidebar-hint {
  font-size: 12px;
  color: var(--muted);
  line-height: 1.4;
}

.recent-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.outline-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 13px;
}

.outline-item {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  padding: 6px 8px;
  border-radius: 8px;
  border: 1px solid transparent;
}

.outline-item:hover {
  border-color: var(--accent);
}

.outline-depth {
  width: 14px;
  text-align: center;
  font-size: 10px;
  color: var(--muted);
}

.outline-text {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.recent-list li {
  padding: 8px 10px;
  border-radius: 10px;
  background: var(--panel-strong);
  border: 1px solid var(--border);
  cursor: pointer;
  font-size: 13px;
  word-break: break-all;
}

.recent-list li:hover {
  border-color: var(--accent);
}

.editor-pane,
.preview-pane {
  background: var(--panel);
  border-radius: 16px;
  padding: 16px;
  border: 1px solid var(--border);
  display: flex;
  flex-direction: column;
  min-height: 0;
}

.preview-pane {
  display: none;
}

#app.split-view .preview-pane {
  display: flex;
}

.pane-title {
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.16em;
  color: var(--muted);
  margin-bottom: 12px;
}

.editor {
  flex: 1;
  min-height: 0;
  border-radius: 12px;
  overflow: hidden;
  background: var(--panel-strong);
  border: 1px solid var(--border);
}

.preview {
  flex: 1;
  overflow: auto;
  padding: 12px 16px;
  background: var(--panel-strong);
  border-radius: 12px;
  border: 1px solid var(--border);
  font-family: var(--font-ui);
}

.preview h1,
.preview h2,
.preview h3,
.preview h4 {
  margin-top: 1.4em;
}

.preview code {
  font-family: var(--font-mono);
  font-size: 0.9em;
}

.preview pre {
  background: rgba(0, 0, 0, 0.08);
  padding: 12px;
  border-radius: 10px;
  overflow: auto;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 16px;
  background: var(--panel-strong);
  border-radius: 16px;
  border: 1px solid var(--border);
  font-size: 13px;
  color: var(--muted);
}

.meta {
  display: flex;
  gap: 16px;
}

.dirty {
  font-weight: 600;
}

.file-path {
  font-family: var(--font-mono);
  font-size: 12px;
}

.hidden {
  display: none;
}

@media (max-width: 1100px) {
  .layout {
    grid-template-columns: 200px 1fr;
  }

  #app.split-view .preview-pane {
    grid-column: 1 / -1;
  }
}

@media (max-width: 800px) {
  .layout {
    grid-template-columns: 1fr;
  }

  .sidebar {
    order: 2;
  }

  .preview-pane {
    order: 3;
  }
}
